import React from "react";
import classes from './Contact.module.css';
import mapimg from '../../assets/map.jpg';

const Contact = props => {
    return (
        <div className={classes.contactContainer} id={props.id}>
            <div className="w3-container w3-content" style={{ width: "100%", backgroundColor: "#ffffff" }}>
                <h2 className="w3-wide w3-center">KAPCSOLAT</h2>
                <p className="w3-opacity w3-center"><i>Rendezvényszervezéssel, foglalással kapcsolatos kérdéseiddel bátran keress meg.</i></p>
                <div className="w3-row w3-padding-32">
                    <div className="w3-col m6 w3-large w3-margin-bottom">
                        <i className="fa fa-map-marker" style={{ width: "30px" }}></i> Város, Ország<br />
                        <i className="fa fa-phone" style={{ width: "30px" }}></i> Telefon: +00 13131313<br />
                        <i className="fa fa-envelope" style={{ width: "30px" }}> </i> Email: mail@mail.com<br />
                    </div>
                    <div className="w3-col m6">
                        <form action="/">
                            <div className="w3-row-padding" style={{ margin: "0 -16px 8px -16px" }}>
                                <div className="w3-half">
                                    <input className="w3-input w3-border" type="text" placeholder="Név" required name="Name" />
                                </div>
                                <div className="w3-half">
                                    <input className="w3-input w3-border" type="text" placeholder="Email" required name="Email" />
                                </div>
                            </div>
                            <input className="w3-input w3-border" type="text" placeholder="Üzenet" required name="Message" />
                            <button className="w3-button w3-black w3-section w3-right" type="submit">KÜLDÉS</button>
                        </form>
                    </div>
                </div>
                <img src={mapimg} alt="map" className="w3-image w3-greyscale-min" style={{ width: "100%" }} />
            </div>
            <br/><br/>
        </div>
    )
}

export default Contact;