import React, { useEffect, useRef, useState } from 'react';
import cls from './Landing.module.css';

import barImg from '../../assets/bar.jpg';
import barImg2 from '../../assets/bar2.jpg';
import barImg3 from '../../assets/bar3.jpg';

const Landing = props => {
    const landingRef = useRef();
    const [myIndex, setMyIndex] = useState(1);

    const myImgs = [ barImg, barImg2, barImg3 ];

    const txts = [
        {
            title: "SZENDVICSEK",
            shortdesc: "Akkor is szívesen látunk, amikor megéhezel. Fogyassz el nálunk egy finom reggelit, különleges kávéink kíséretében."
        },
        {
            title: "MINŐSÉGI SÖRKÍNÁLAT",
            shortdesc: "Számos fajta kiváló minőségű sörrel várunk, melyeket nagy odafigyeléssel csapolunk."
        },
        {
            title: "KELLEMES HANGULAT",
            shortdesc: "Kisebb és nagyobb társaságok számára is vannak asztalaink. Minden pénteken koncertekkel, egyéb rendezvényekkel várunk."
        },
    ];


    useEffect(() => {
        const carousel = setInterval(() => {
            var x = myImgs;
            setMyIndex((myIndex) => myIndex+1);
            if (myIndex >= x.length-1) { setMyIndex(0); }
            landingRef.current.style.animation ="none";
            landingRef.current.style.backgroundImage = `linear-gradient(to bottom, rgba(0, 0, 0, 0) 70%, rgba(0, 0, 0, 1)), url(${myImgs[myIndex]})`;
            //reset animation to avoid misalignment between carousel interval and animation duration
            landingRef.current.style.animation ="";
        }, 10000);

        return () => clearInterval(carousel);
    }, [myIndex,myImgs]);

    return (
        <div ref={landingRef} className={cls.landingContent} id={props.id} >
            <div className={cls.landingTxtContainer}>
                <h3>{myIndex < 3 && txts[myIndex].title}</h3>
                <p>{myIndex < 3 && txts[myIndex].shortdesc}</p>
            </div>
        </div>
    )
}

export default Landing;