import React from 'react';
import { HashLink as RouterLink } from 'react-router-hash-link';

const Navbar = props => {

    // Used to toggle the menu on small screens when clicking on the menu button
    const handleMenu = () => {
        var t = document.getElementById("navDemo");
        if ( !t.classList.contains("w3-show") ) {
            t.classList.add("w3-show");
        } else {
            t.classList.remove("w3-show");
        }
    }

    return (
        <>
            <div className="w3-top">
                <div className="w3-bar w3-black w3-card">
                    <a className="w3-bar-item w3-button w3-padding-large w3-hide-medium w3-hide-large w3-right" href={process.env.PUBLIC_URL + '/pub'} onClick={handleMenu} title="Toggle Navigation Menu"><i className="fa fa-bars"></i></a>
                    <RouterLink to={process.env.PUBLIC_URL + '#top'} className="w3-bar-item w3-button w3-padding-large">KEZDŐLAP</RouterLink>
                    <RouterLink to={process.env.PUBLIC_URL + '#events'} className="w3-bar-item w3-button w3-padding-large w3-hide-small">ESEMÉNYEK</RouterLink>
                    <RouterLink to={process.env.PUBLIC_URL + '#about'} className="w3-bar-item w3-button w3-padding-large w3-hide-small">RÓLUNK</RouterLink>
                    <div className="w3-dropdown-hover w3-hide-small">
                        <button className="w3-padding-large w3-button" title="More">EGYEBEK <i className="fa fa-caret-down"></i></button>
                        <div className="w3-dropdown-content w3-bar-block w3-card-4">
                            <a href={process.env.PUBLIC_URL + '#top'} className="w3-bar-item w3-button">Reklám termékek</a>
                            <a href={process.env.PUBLIC_URL + '#top'} className="w3-bar-item w3-button">Galéria</a>
                        </div>
                    </div>
                    <a href={process.env.PUBLIC_URL + '#contact'} className="w3-bar-item w3-button w3-padding-large w3-hide-small">KAPCSOLAT</a>
                    <a href={process.env.PUBLIC_URL + '#top'} className="w3-padding-large w3-teal w3-hover-red w3-hide-small w3-right"><i className="fa fa-instagram"></i></a>
                    <a href={process.env.PUBLIC_URL + '#top'} className="w3-padding-large w3-blue w3-hover-red w3-hide-small w3-right"><i className="fa fa-facebook"></i></a>
                </div>
            </div>
            <div id="navDemo" className="w3-bar-block w3-black w3-hide w3-hide-large w3-hide-medium w3-top" style={{ marginTop: '46px' }}>
                <a href={process.env.PUBLIC_URL + '#events'} className="w3-bar-item w3-button w3-padding-large" onClick={handleMenu}>ESEMÉNYEK</a>
                <a href={process.env.PUBLIC_URL + '#about'} className="w3-bar-item w3-button w3-padding-large" onClick={handleMenu}>RÓLUNK</a>
                <a href={process.env.PUBLIC_URL + '#contact'} className="w3-bar-item w3-button w3-padding-large" onClick={handleMenu}>KAPCSOLAT</a>
            </div>
        </>
    )
}

export default Navbar;