import React, {useRef, useEffect} from 'react';

import koncert from '../../assets/koncert.jpg';
import koncert2 from '../../assets/koncert2.jpg';
import comedy from '../../assets/comedy.jpg';
const Event = props => {
    const modalRef = useRef();

    const openModal = () => {
        modalRef.current.style.display='block';
    }

    const closeModal = () => {
        modalRef.current.style.display='none';
    }

    useEffect(() => {
        window.onclick = function(event) {
            if (event.target === modalRef.current) {
                modalRef.current.style.display = "none";
            }
          }
    }, [])

    return (
        <>
            <div id={props.id} className="w3-black">
                <div className="w3-container w3-content w3-padding-64" style={{ maxWidth: "800px" }}>
                    <h2 className="w3-wide w3-center">ESEMÉNYEK</h2>
                    <p className="w3-opacity w3-center"><i>Néhány esemény jeggyel látogatható.</i></p><br />

                    <div className="w3-row-padding w3-padding-32" style={{ margin: "0 -16px" }}>
                        <div className="w3-third w3-margin-bottom">
                            <img src={koncert2} alt="koncert" style={{ width: "100%" }} className="w3-hover-opacity" />
                            <div className="w3-container w3-white">
                                <p><b>Koncert</b></p>
                                <p className="w3-opacity">2021 November 5. 18:00</p>
                                <p>Az XY zenekar akusztikus előadása.</p>
                                <button className="w3-button w3-black w3-margin-bottom" >Ingyenes</button>
                            </div>
                        </div>
                        <div className="w3-third w3-margin-bottom">
                            <img src={koncert} alt="koncert" style={{ width: "100%" }} className="w3-hover-opacity" />
                            <div className="w3-container w3-white">
                                <p><b>Koncert</b></p>
                                <p className="w3-opacity">2021 November 12. 21:00</p>
                                <p>Neves zenekar koncertje. Belépő ára: 2000 Ft.</p>
                                <button className="w3-button w3-black w3-margin-bottom" onClick={openModal}>Jegyvásárlás</button>
                            </div>
                        </div>
                        <div className="w3-third w3-margin-bottom">
                            <img src={comedy} alt="comedy" style={{ width: "100%" }} className="w3-hover-opacity" />
                            <div className="w3-container w3-white">
                                <p><b>Stand up comedy est</b></p>
                                <p className="w3-opacity">2021 Novemer 19. 20:00</p>
                                <p>XYZ stand up comedy páros előadása. Jegyár: 4000 Ft.</p>
                                <button className="w3-button w3-black w3-margin-bottom" onClick={openModal}>Jegyvásárlás</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div ref={modalRef} id="ticketModal" className="w3-modal">
                <div className="w3-modal-content w3-animate-top w3-card-4">
                    <header className="w3-container w3-teal w3-center w3-padding-32">
                        <span onClick={closeModal}
                            className="w3-button w3-teal w3-xlarge w3-display-topright">×</span>
                        <h2 className="w3-wide"><i className="fa fa-suitcase w3-margin-right"></i>Jegyek</h2>
                    </header>
                    <div className="w3-container">
                        <p><label><i className="fa fa-shopping-cart"></i> Jegyek, 2000 Ft / fő</label></p>
                        <input className="w3-input w3-border" type="text" placeholder="Mennyi?" />
                        <p><label><i className="fa fa-user"></i> Hova küldjük a jegyeket és a számlát?</label></p>
                        <input className="w3-input w3-border" type="text" placeholder="Email címed" />
                        <button className="w3-button w3-block w3-teal w3-padding-16 w3-section w3-right">FIZETÉS <i className="fa fa-check"></i></button>
                        <button className="w3-button w3-red w3-section" onClick={closeModal}>Bezárás <i className="fa fa-remove"></i></button>
                        <p className="w3-right">Szükséged van <a href="/#" className="w3-text-blue">segítségre?</a></p>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Event;