import React, { useEffect } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import { HashLink as RouterLink } from 'react-router-hash-link';
import CookieConsent from 'react-cookie-consent';

import Navbar from './components/Navbar/Navbar';
import Landing from './components/Landing/Landing';
import Events from './components/Events/Events';
import About from './components/About/About';
import Contact from './components/Contact/Contact';
import Footer from './components/Footer/Footer';

function App() {

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <Router>
      <CookieConsent
        debug={false}
        location="bottom"
        contentClasses={"cookieConsent"}
        buttonText="Elfogadom"
        expires={1}
        overlay
      >
        Ez a weboldal a felhasználói élmény fokozásának érdekében sütiket alkalmaz. Az Adatkezelési tájékoztatóban foglaltakat elolvastam, megértettem és elfogadom.&nbsp;&nbsp;&nbsp;
        <RouterLink to="/">Adatkezelési tájékoztató</RouterLink>
      </CookieConsent>

      <Navbar />

      <Switch>
        <Route exact path={process.env.PUBLIC_URL + '/pub'}>
          <Landing id="top" />
          <Events id="events" />
          <About id="about" />
          <Contact id="contact" />
        </Route>
      </Switch>
      <Footer />
    </Router>
  );
}

export default App;
